import { GetToken } from "@/utilities/cookies";
import OrderDetail from "@/Components/OrderHistory/OrderDetail";
import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "Order";
  return createMetadata({
    title: title,
  });
}
export default async function page(props) {
  const params = await props.params;
  const token = await GetToken();

  return <OrderDetail token={token} params={params?.id} />;
}
